import React from "react"
import SwiperCore, { Navigation, Pagination } from "swiper"
import { Swiper, SwiperSlide } from "swiper/react"
import styled from "styled-components"
import "swiper/swiper-bundle.min.css"
import SlideshowImage from "./slideshow-image"
import { useStaticQuery, graphql } from "gatsby"

const StyledSwiper = styled.div`
  align-items: center;
  /* margin-top: ${props => props.theme.margins.marginLarge}; */
`

const StyledSlide = styled.div`
  align-items: center;
`

SwiperCore.use([Navigation, Pagination])

const Slideshow = ({ foldername }) => {
  const data = useStaticQuery(graphql`
    query {
      images: allFile {
        edges {
          node {
            relativePath
            name
            publicURL
            childImageSharp {
              fluid(maxWidth: 1200) {
                ...GatsbyImageSharpFluid_noBase64
              }
            }
          }
        }
      }
    }
  `)

  let filenames = []

  return (
    <StyledSwiper>
      <Swiper
        spaceBetween={0}
        slidesPerView={1}
        navigation
        preloadImages={true}
      >
        {data.images.edges.map(({ node }) => {
          let relativePath = node.relativePath
          if (
            relativePath.includes(foldername) &&
            relativePath.includes(".png")
          ) {
            filenames.push(node.publicURL)
          }
        })}

        {filenames.map(file => (
          <SwiperSlide key={file}>
            <StyledSlide>
              <SlideshowImage filename={file} />
            </StyledSlide>
          </SwiperSlide>
        ))}
      </Swiper>
    </StyledSwiper>
  )
}

export default Slideshow
